export function zeroFormatter(hour, minutes, seconds) {
   return ([
      hour.toString().padStart(2, '0'),
      minutes.toString().padStart(2, '0'),
      seconds.toString().padStart(2, '0')
   ].join(':'));
}

export function secToTime(timeStamp) {
   let h = Math.floor(timeStamp / 3600);
   let m = Math.floor(timeStamp / 60) - (h * 60);
   let s = Math.floor(timeStamp % 60);
   return [h, m, s];
}

export function decOfNum(number, titles) {
   let decCache = [],
      decCases = [2, 0, 1, 1, 1, 2];
   if (!decCache[number]) decCache[number] = number % 100 > 4 && number % 100 < 20 ? 2 : decCases[Math.min(number % 10, 5)];
   return titles[decCache[number]];
}

export function timeFormattedString(timeStamp) {
   const [h, m, s] = secToTime(timeStamp);
   let valueFormat = '';
   if (h > 0) {
      valueFormat = h + ' ' + decOfNum(h, ['час', 'часа', 'часов']);
   }
   if (m > 0) {
      valueFormat += ' ' + m + ' ' + decOfNum(m, ['минута', 'минуты', 'минут']);
   }
   if (s > 0 && h === 0) {
      valueFormat += ' ' + s + ' ' + decOfNum(s, ['секунда', 'секунды', 'секунд']);
   }

   return valueFormat;
}

export function timeFormattedColon(timeStamp) {
   const [h, m, s] = secToTime(timeStamp);
   return zeroFormatter(h, m, s);
}