import { Grid, Typography } from "@mui/material";
import CardNode from "./CardNode";
import ValueCard from "components/cards/ValueBox/ValueCard";
import dataLine from "assets/dataLine";
import { useParams } from "react-router-dom";
import Service from "API/Service";
import { useQuery } from "react-query";
import { timeFormattedColon } from "utils/timeFormatter";
import priceFormatter from "utils/priceFormatter";


let initialData = dataLine;


const format = (data) => {
    let valueFormat, unitFormat = '';
    const { value, unit } = data;

    if (unit === 'time') {
        if (value > 0) {
            valueFormat = timeFormattedColon(value);
        } else {
            valueFormat = 0;
        }
    } else if (typeof value === 'string' || value instanceof String) {
        valueFormat = value;
    } else {
        unitFormat = unit;
        valueFormat = priceFormatter(value);
    }

    return { ...data, unit: unitFormat, value: valueFormat }
}


const Line = () => {
    const { id } = useParams();

    const { isLoading, isError, data, error } = useQuery(
        ['arrProgram', { programPack: initialData[id].lineArray }], () => Service.getProgramPackArray({ programPack: initialData[id].lineArray }), {
        select: (data) => data.p,
        onSuccess: (data) => data?.forEach((element) => {
            initialData[id].lineValue?.forEach((el) => {
                if (element._id === el.id) {
                    el.value = Number(element.lastAnswerDec);
                }
            });
            initialData[id].nodes?.forEach((node) => {
                node.value?.forEach((node) => {
                    if (element._id === node.id) {
                        node.value = Number(element.lastAnswerDec);
                    }
                });
            });
        }),
        refetchInterval: 1000,
        enabled: initialData[id].lineArray.length !== 0,
        refetchOnWindowFocus: false
    });


    return (
        <Grid container rowSpacing={4.5} columnSpacing={2.75}>
            <Grid item xs={12} sx={{ mb: -2.25 }}>
                <Typography variant="h4">{initialData[id].lineName}</Typography>
            </Grid>
            <Grid container item spacing={3}>
                {initialData[id].lineValue.map((el) =>
                    <Grid item xs={12} sm={6} md={4} lg={3}>
                        <ValueCard data={format(el)} />
                    </Grid>
                )}
            </Grid>
            <Grid item xs={12} >
                <Typography variant="h4">Узлы</Typography>
            </Grid>
            <Grid container item spacing={3}>
                {initialData[id].nodes.map((node) =>
                    <Grid item xs={12}>
                        <CardNode props={node} />
                    </Grid>
                )}
            </Grid>
        </Grid>
    );
}


export default Line;