import { Box, CardMedia, Grid, List, Typography, useTheme } from "@mui/material";
import MainCard from "components/MainCard";
import Content from "pages/manufacture/CardLine/CardDetails/CardContent/Content";

const CardNode = ({ props }) => {
    const { name, url, value } = props;
    const theme = useTheme();
    return (
        <MainCard contentSX={{ p: 3, '&:last-child': { p: 3 } }} >
            <Grid container spacing={3}>
                <Grid container item xs={12} sm={4} md={4} lg={3} justifyContent={'center'}>
                    <Grid container item sx={{ width: '100%', backgroundColor: theme.palette.grey[50], borderRadius: '20px', p: 2 }} justifyContent={'center'}>
                        <CardMedia
                            component="img"
                            sx={{ maxWidth: 350 }}
                            image={url}
                            alt={name}
                        />
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={8} md={8} lg={9}>
                    <Box sx={{ pt: 2, pb: 2, borderBottom: '1px solid #f0f0f0' }}>
                        <Typography variant="h5">{name}</Typography>
                    </Box>
                    <List sx={{ width: '100%', p: 0 }}>
                        {value.map((el) => <Content props={el} />)}
                    </List>
                </Grid>
            </Grid>
        </MainCard>
    );
}

export default CardNode;