import { Box, LinearProgress, ListItem, ListItemText, Typography, useMediaQuery, useTheme } from "@mui/material";
import { timeFormattedColon, timeFormattedString } from "utils/timeFormatter";
import priceFormatter from "utils/priceFormatter";


const Content = ({ props }) => {
    const { name, value, progress, unit } = props;
    const theme = useTheme();
    const matchDownMS = useMediaQuery(theme.breakpoints.only('xs'));
    let valueFormat, unitFormat = '';

    if (unit === 'time') {
        if (value > 0) {
            valueFormat = matchDownMS ? timeFormattedColon(value) : timeFormattedString(value);
        } else {
            valueFormat = 0;
        }
    } else if (typeof value === 'string' || value instanceof String) {
        valueFormat = value;
    } else {
        unitFormat = unit;
        valueFormat = isNaN(value) ? 0 : priceFormatter(value);
    }

    return (
        <ListItem item sx={{ p: 0, pt: 1.25, pb: 1.25, borderBottom: '1px solid #f0f0f0', flexWrap: matchDownMS ? 'wrap' : 'nowrap' }} >
            <ListItemText sx={{ mr: 1 }} primary={<Typography variant="body1">{name} </Typography>} />

            {!progress && <Typography sx={{ textAlign: 'right' }} variant="h5">{`${valueFormat} ${unitFormat}`} </Typography>}

            {progress &&
                <Box sx={{ minWidth: matchDownMS ? '100%' : '40%', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <Box sx={{ width: '100%', mr: 1 }}>
                        <LinearProgress color="primary" variant="determinate" value={isNaN(value) ? 0 : Math.round(value)} />
                    </Box>
                    <Box sx={{ minWidth: 35 }}>
                        <Typography sx={{ textAlign: 'right' }} variant="h5">{`${isNaN(value) ? 0 : Math.round(value)}% `}</Typography>
                    </Box>
                </Box>
            }
        </ListItem>
    );
}


export default Content;